import {makeAutoObservable} from 'mobx'
import {http} from '@/utils'

class ChannelStore {
    channelList = []

    constructor() {
        makeAutoObservable(this)
    }

    // article publish 
    loadChannelList = async () => {
        const {data} = await http.get('/category/index')
        this.channelList = data || [];
    }
}

export default ChannelStore